body {
  font-family: var(--font-family-noto_sans_jp);
  -moz-osx-font-smoothing: unset;
  flex-direction: column;
  padding-top: 0;
  padding-bottom: 0;
  font-size: 1rem;
  font-weight: 400;
}

main {
  grid-template-rows: auto;
  grid-template-columns: auto;
  width: 100%;
  display: grid;
}

.l-header {
  z-index: 10;
  background: #fff;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 3px 6px #00000029;
}

.l-header__container {
  height: 60px;
  padding-left: 20px;
  display: flex;
}

.l-header__button {
  background: #1c77a8;
  width: 60px;
  height: 60px;
  margin-left: auto;
  display: block;
  position: relative;
}

.l-header__button span {
  background: #fff;
  width: 28px;
  height: 3px;
  transition: transform .3s, top .3s .3s;
  display: block;
  position: absolute;
  top: 23px;
  left: calc(50% - 14px);
}

.l-header__button span:nth-child(2) {
  top: 34px;
}

.l-header__logo {
  align-self: center;
  width: 128px;
}

.l-header-nav {
  visibility: hidden;
  z-index: 9;
  background: #0000001a;
  width: 100%;
  height: calc(100% - 60px);
  transition: transform .3s, visibility .3s;
  position: fixed;
  top: 60px;
  left: 0;
  transform: translate3d(100%, 0, 0);
}

.l-header-nav[aria-hidden="false"] {
  visibility: visible;
  transform: translate3d(0, 0, 0);
}

.l-header-nav__body {
  background: #fff;
  width: calc(100% - 85px);
  height: 100%;
  max-height: 100%;
  margin-left: auto;
  padding: 25px 20px 40px;
  overflow-y: auto;
}

.l-header-nav__buttons {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 15px;
  margin-top: 25px;
  display: grid;
}

.l-header-nav__list-item {
  border-bottom: 1px dashed #1c77a8;
}

.l-header-nav__button:first-child {
  background: #1c77a8;
}

.l-header-nav__list-item a {
  align-items: center;
  padding: 25px 0;
  font-size: 1rem;
  font-weight: 700;
  display: flex;
}

.l-header-nav__button {
  color: #fff;
  justify-content: center;
  align-items: center;
  height: 50px;
  font-size: 1rem;
  font-weight: 700;
  display: flex;
}

.l-header-nav__button:nth-child(2) {
  background: #e67614;
}

a {
  color: inherit;
  background-color: #0000;
  text-decoration: none;
}

img {
  vertical-align: bottom;
  max-width: 100%;
  height: auto;
}

.c-breadcrumb {
  background: #f6f6f6;
  align-items: center;
  min-height: 45px;
  padding: 10px 20px;
  font-size: .8125rem;
  display: flex;
}

.c-breadcrumb__list {
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  display: flex;
}

.c-breadcrumb__item a {
  color: #166c99;
}

.c-breadcrumb__item + .c-breadcrumb__item:before {
  content: ">";
  margin: 0 5px;
}

.l-footer {
  color: #fff;
  background: #000;
  margin-top: auto;
}

.l-footer__container {
  border-top: 1px solid #9a9a9a;
  border-bottom: 1px solid #9a9a9a;
  width: 100%;
  padding: 25px 20px;
}

.l-footer__copy {
  margin-top: 15px;
  font-size: .875rem;
  display: block;
}

.l-footer__list {
  align-items: center;
  gap: 30px;
  display: flex;
}

.l-footer__list a {
  align-items: center;
  font-size: .875rem;
  text-decoration: underline;
  display: flex;
}

@media (width >= 48.0625em) {
  body {
    padding-top: 0;
    padding-bottom: 0;
  }

  .l-header__container {
    justify-content: space-between;
    height: 70px;
    padding: 0 min(4%, 70px);
  }

  .l-header__logo {
    width: 178px;
  }

  .l-header__button {
    visibility: hidden;
    display: none;
  }

  .l-header-nav {
    background: none;
    width: auto;
    height: auto;
    transition: none;
    position: static;
  }

  .l-header-nav__body {
    background: none;
    width: auto;
    padding: 0;
    display: flex;
  }

  .l-header-nav__list {
    align-items: center;
    gap: 30px;
    display: flex;
  }

  .l-header-nav__list-item a {
    padding: 0;
    display: inline;
  }

  .l-header-nav__buttons {
    grid-template-columns: 123px 123px;
    align-items: center;
    margin-top: 0;
    margin-left: 30px;
  }

  .l-header-nav__list-item--sp-only {
    display: none;
  }

  .l-header-nav__list-item {
    border: none;
  }

  .l-header-nav__button:first-child {
    color: #1c77a8;
    background: none;
    border: 2px solid #1c77a8;
  }

  .l-header-nav__button {
    border-radius: 100vmax;
    height: 36px;
  }

  .l-header-nav__button:nth-child(2) {
    color: #e67614;
    background: none;
    border: 2px solid #e67614;
  }

  .c-breadcrumb {
    min-height: 55px;
    padding: 10px 0;
  }

  .c-breadcrumb__list {
    width: min(94%, 1520px);
    margin: auto;
  }

  .l-footer__container {
    justify-content: space-between;
    align-items: center;
    min-height: 60px;
    padding: 30px min(4%, 70px);
    display: flex;
  }

  .l-footer__copy {
    margin-top: 0;
  }

  .l-footer__list a {
    font-size: .9375rem;
  }

  .l-footer__list a:after {
    width: 15px;
    height: 15px;
  }
}
